@import "./utils";

@media (max-width: 1500px) {
	.hero {
		min-height: 514px;

		&_info {
			padding-top: 152px;
		}

		&_title {
			font-size: 47px;
		}

		&_subtitle {
			line-height: 15px;
			font-size: 22px;
		}

		&_text {
			max-width: 300px;
			font-size: 12px;
			line-height: 1.2;
			margin-bottom: 28px;
		}
	}
	.cash_title, .title {
		font-size: 42px;
		margin-bottom: 10px;
	}

	.footer_item {
		margin-bottom: 0;
	}

	.subtitle {
		font-size: 19px;
	}

	.cash .cash_title_group {
		margin-bottom: 40px;
	}

	.flip_card {
		width: 192px;
		height: 216px;

		.front, .back {
			padding: 40px 10px;
			font-size: 15px;
			line-height: 1.3;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			img {
				display: inline-block;
				width: auto;
				height: 100px;
				margin: 0 auto 22px;;
			}
		}


		.flip_title {
			font-size: 19px;
			line-height: 19px;
			color: #324DA1;
		}
	}

	.flip_list {
		width: 1060px;
	}

	.why {
		padding-top: 110px;
		min-height: 512px;

		.title {
			font-size: 41px;
			margin-bottom: 19px;
		}

		&_text {
			margin-top: 22px;
			margin-bottom: 28px;
			font-size: 24px;
			line-height: 1.5;
			max-width: 748px;
		}
	}
	.industries {
		padding-top: 110px;
		padding-bottom: 12px;
	}
	.cash_title {
		font-size: 37px;
		line-height: 1.28;
		margin-bottom: 20px;
	}

	.cash_subtitle {
		font-size: 18px;
		margin-bottom: 8px;
	}

	.industries_text {
		font-size: 15px;
		padding-right: 78px;
		margin-bottom: 22px;
	}
	.cash {
		overflow: hidden;
		.cash_title {
			margin-bottom: 20px;
		}

		.cash_text {
			font-size: 19px;
			line-height: 1.5;
			padding-right: 100px;
		}

		img {
			display: block;
			width: 362px;
			border-style: none;
			margin: 11px 0 0 135px;
		}
	}

	.statistic {
		padding-top: 15px;
		padding-bottom: 40px;

		.title {
			font-size: 37px;
			line-height: 1;
			margin-bottom: 54px;
		}

		.statistic_img {
			width: 225px;
			padding: 29px 25px;
		}

		.statistic_item:nth-child(1) .statistic_title {
			left: 123px;
		}

		.statistic_item:nth-child(2) .statistic_title {
			left: 65px;
		}

		.statistic_item:nth-child(3) {
			left: -100px;
		}

		.statistic_item:nth-child(3) .statistic_title {
			left: 24px;
		}
	}

	.statistic_title {
		font-size: 19px;
		line-height: 1;
	}
	.statistic_item {
		padding-top: 30px;
		padding-bottom: 34px;
	}

	.statistic_line {
		width: 417px;
		height: 22px;
	}
	.statistic_point {
		width: 35px;
		height: 35px;
	}
	.statistic_number {
		font-size: 24px;
		line-height: 1;
		left: 440px;
		top: 34px;
	}

	.section.section_reviews {
		padding-top: 53px;
		padding-bottom: 81px;

		.title {
			font-size: 38px;
			line-height: 37px;
			margin-bottom: 11px;
		}

		.subtitle {
			font-size: 19px;
			line-height: 1;
			margin-bottom: 44px;
		}
	}

	.testimonials_card {
		width: 262px;
		height: 358px;
		padding: 26px 21px;
	}

	.testimonials_photo {
		width: 112px;
		height: 112px;
		object-fit: cover;
	}

	.testimonials_title {
		font-size: 22px;
		line-height: 1;
		margin-bottom: 15px;
	}
	.testimonials_text {
		font-size: 15px;
		line-height: 1.3;
	}

	.section_reviews {
		.title.approve_title {
			font-size: 41px;
			line-height: 1.2;
			margin-bottom: 11px;
		}

		.subtitle.approve_text {
			margin-bottom: 38px;
		}

		.col--right {
			img {
				width: 160%;
			}
		}
	}
	.section.section_calculator .col--left {
		padding-left: 144px;
	}
	.section.section_calculator .col--right {
		padding-left: 37px;
	}
	.section_calculator {
		.title {
			font-size: 38px;
			margin-bottom: 60px;
		}

		.col--right {
			flex-basis: 40%;
			width: 40%;

			img {
				width: 67%;
			}
		}

		.col--left {
			flex-basis: 50%;
			width: 50%;

			.form_control {
				height: 52px;
				width: 370px;
				font-size: 18px;
				line-height: 1;
				padding: 18px 22px;

				&::placeholder {
					font-size: 18px;
					line-height: 1;
				}
			}
		}
	}

	.footer {
		padding: 30px 0 20px;

		address {
			line-height: 1.6;
		}
	}
	.footer_title {
		font-size: 18.75px;
		line-height: 19px;
		margin-bottom: 9px;
	}
	.footer_partner:nth-child(1) {
		img {
			width: 60px;
		}
	}
	.footer_partner:nth-child(2) {
		img {
			width: 80px;
		}
	}
	.footer_partner:nth-child(3) {
		img {
			width: 80px;
		}
	}
	.footer_link {
		font-size: 15px;
		line-height: 15px;
		margin-bottom: 10px;
	}
	.footer_logo {
		width: 130px;
		margin-bottom: 8px;

		img {
			width: 100%;
		}
	}

	.up_btn {
		width: 40px;
		height: 40px;
		right: -26px;
		top: -8px;
	}

	.flip_card--first .back,
	.flip_card--first .front,
	.flip_card--second .back,
	.flip_card--second .front,
	.flip_card--third .back,
	.flip_card--third .front {
		background-image: none;
		overflow: hidden;
	}

	.flip_list .rect_top_left--top {
		top: -25px;
		left: -25px;
	}

	.slider_wrapper {
		img {
			max-width: 100%;
		}
	}
}

@media (max-width: 1279px) {
	.body {
		overflow: hidden;
	}

	.flip_list {
		width: 100%;
	}
	.cash .cash_text {
		font-size: 12px;
		line-height: 1.5;
		padding-right: 0;
	}

	.up_btn {
		right: 12px;
	}

	.main_nav .nav_list {
		right: calc((100vw - 1150px) / 2 - 20px);
	}
}

@media (max-width: 1024px) {
	.header_mobile {
		.light_char {
			fill: #fff;
		}
	}

	.nav_mobile {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 10;
		width: 100%;
		height: 80px;
		background: transparent;
		box-shadow: none;
		padding: 0;
		border-radius: 0;

		.nav_btn_open,
		.nav_btn_close {
			position: absolute;
			top: 20px;
			right: 20px;
		}
		.nav_list {
			right: 25px;
			background: transparent;
			box-shadow: none;
			padding: 0;
			margin: 0;
		}

		.nav_item {
			opacity: 0.5;
			font-size: 17px;
			line-height: 17px;
			margin-bottom: 20px;
			border: none;

			&.active {
				opacity: 1;
				border: none;

				.nav_link {
					color: #fff;
				}
			}
		}

		.nav_link {
			padding-right: 0;
		}

		.nav_link {
			color: #fff;
			line-height: 37px;
		}
	}

	.header_mobile {
		.logo {
			width: 145px;
			position: absolute;
			top: 20px;
			left: 20px;
			z-index: 200;
		}
	}
	.header_mobile.menu_show {
		.nav_mobile {
			height: 100vh;
			padding: 100px 25px;
			transition: background-color 0.4s ease 0.2s;
		}
	}

	.header_mobile:not(.header--top) {
		width: 100%;
		height: 80px;
		background-color: rgba(50,77,171,0.85);
		box-shadow: 0 0 40px rgba(121, 255, 142, 0.15);
		transition: background-color 0.4s ease 0.2s;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 200;
	}
	.header_mobile.header--top:not(.menu_show) {
		box-shadow: none;
	}

	.header_mobile.menu_show {
		width: 100%;
		z-index: 200;
		height: 100vh !important;
		background-color: rgba(50,77,171,0.85) !important;
		transition: background-color 0.4s ease 0.2s;
		box-shadow: none !important;
	}

	.btn {
		height: 62px;
		font-size: 21px;
		line-height: 21px;
		border-radius: 30px;
		width: 100%;
	}
	.hero_title {
		font-size: 46.2633px;
		line-height: 120%;
		text-align: center;
		margin-bottom: 17px;
		letter-spacing: 1.01px;
	}

	.hero_subtitle {
		font-size: 20.8185px;
		line-height: 135%;
		margin-bottom: 5px;
		text-align: center;
	}

	.hero_text {
		font-family: helveticaRegular, sans-serif;
		font-size: 17.3488px;
		line-height: 150%;
		margin: 0 auto 60px;
		text-align: center;
	}

	.hero_info {
		padding-top: 125px;
	}

	.hero .btn {
		width: 100%;
	}
	.fixed_btn {
		display: none;
	}

	.section {
		padding: 80px 0 102px;

		&.hero {
			padding: 100px 0 230px;
		}

		&.why {
			padding: 80px 0;
		}
	}

	.title {
		font-size: 35px;
		line-height: 35px;
		margin-bottom: 15px;
	}

	.subtitle {
		font-size: 15px;
		line-height: 150%;
		margin-bottom: 35px;
		padding: 0 15px;
	}

	.flip_list {
		display: block;
		flex-wrap: wrap;
		margin-bottom: 45px;
	}
	.flip_card {
		margin: auto;
	}

	.flip_card--first,
	.flip_card--second {
		margin-bottom: 45px;

		&:after {
			width: 25px;
			height: 15px;
			top: initial;
			bottom: -28px;
			left: 50%;
			transform: rotate(90deg) translateY(14px);
		}
	}

	.why {
		min-height: 450px;
		text-align: left;

		.title {
			font-size: 35px;
			line-height: 35px;
			margin-bottom: 20px;
			text-align: left !important;
		}

		&_text {
			font-size: 20px;
			line-height: 150%;
			text-align: left;
			margin-bottom: 0;
			padding-right: 12px;
		}
	}
	.industries {
		&.section {
			padding-top: 72px;
			padding-bottom: 0;
		}

		.container {
			overflow: hidden;
			padding: 0 15px;
		}

		.cash_title {
			text-align: center;
			font-size: 35px;
			line-height: 120%;
			margin-bottom: 15px;
		}

		.cash_subtitle {
			text-align: center;
			font-size: 18px;
			line-height: 135%;
			margin-bottom: 0;
		}
	}
	.slider_wrapper {
		width: 610px;
		position: relative;
		padding-top: 300px;
		padding-right: 25px;
		padding-left: 25px;

		img {
			max-width: 610px;
			transform: translateX(-50%);
			position: absolute;
			left: 50%;
			top: -20px;
		}
	}

	.cash {
		&#hiw {
			padding-bottom: 80px;
		}

		&.section {
			padding-bottom: 0;
		}

		.cash_title {
			font-size: 35px;
			line-height: 35px;
			margin-bottom: 35px;
		}

		.cash_text {
			font-size: 20px;
			line-height: 150%;
			padding-right: 25px;
		}

		img {
			width: 239px;
			margin: 80px auto 0;
		}
	}
	.statistic {
		padding-bottom: 0;

		.title {
			letter-spacing: -1.1px;
			margin-bottom: 34px;
		}

		.statistic_title {
			left: 0;
			font-size: 18px;
			line-height: 18px;
		}

		.statistic_line {
			width: 100%;
			height: 30px;
			left: 0;
		}


		.statistic_item {
			left: 0 !important;
			padding: 26px 0 45px 20px;

			&:nth-child(1) {
				width: 90%;

				.statistic_title {
					left: 20px;
				}

				.statistic_point {
					left: 76%;
				}
			}

			&:nth-child(2) {
				width: 95%;

				.statistic_title {
					left: 20px;
				}

				.statistic_point {
					left: 80%;
				}
			}

			&:nth-child(3) {
				width: 100%;

				.statistic_title {
					left: 20px;
				}

				.statistic_point {
					left: 68%;
				}
			}
		}

		.statistic_point {
			top: -4px;
			width: 36px;
			height: 36px;
		}

		.statistic_number {
			font-size: 20px;
			line-height: 20px;
			height: initial;
			bottom: 16px;
			top: inherit;
			left: initial;
			right: 30px;
		}
	}

	.section.section_reviews {
		padding-top: 66px;

		.title {
			font-size: 35px;
			line-height: 120%;
			margin: 0 auto 15px auto;
			max-width: 80%;
		}

		.subtitle {
			font-size: 15px;
			line-height: 150%;
			max-width: 240px;
			margin: 0 auto;
		}
	}
	.testimonials_photo {
		margin: 0 auto 10px;
	}
	.testimonials_text {
		max-width: 215px;
		margin: 0 auto 11px;
		font-size: 13.8784px;
		line-height: 130%;
	}
	.testimonials_title {
		margin-bottom: 12px;
	}

	.testimonials_card {
		height: 335px;
		padding: 26px 14px;

		.rect_bottom_right {
			bottom: -4px;
			right: -4px;
		}
		.rect_bottom_left {
			bottom: -4px;
			left: -4px;
		}
		.rect_top_left {
			top: -4px;
			left: -4px;
		}
		.rect_top_right {
			top: -4px;
			right: -4px;
		}
	}

	.testimonials_card:focus,
	.testimonials_list:focus {
		outline: none;
	}

	.mobile_review {
		margin: auto;
		padding: 5px 15px 5px;
		width: 305px;

		.testimonials_card {
			width: 235px;
			margin: 35px;
		}

		.card__img {
			width: 104px;
			height: 104px;
			margin: 21px auto 17px;
		}

		.card_text {
			font-size: 13.8784px;
			line-height: 130%;
		}

		.slick-arrow {
			position: absolute;
			top: 50%;
			background-color: transparent;
			border: none;
			font-size: 0;
			width: 28px;
			height: 15px;
			background-image: url(../images/arrow_right.svg);
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center center;
		}

		.slick-prev {
			left: -16px;
			transform: rotate(180deg) translateY(-50%);
			z-index: 1;
		}

		.slick-next {
			right: -16px;
			top: 53%;
			transform: translateY(-50%);
			z-index: 1;
		}


		}

	.section.section_reviews .col--right,
	.section.section_reviews .col--left {
		position: relative;
		flex-basis: 100%;
		text-align: center;
	}
	.section.section_reviews .col--right {
		min-height: 300px;
	}
	.section.section_reviews .col--left {
		margin-bottom: 20px;
	}
	.section.section_reviews {
		padding-bottom: 74px;
		.title.approve_title  {
			font-size: 35px;
			line-height: 120%;
			margin-bottom: 15px;
			max-width: 100%;
			padding-top: 28px;
		}

		.approve_text {
			font-size: 25px;
			line-height: 25px;
			margin-bottom: 38px;
			max-width: 100%;
		}
	}
	.section_reviews .col--right img {
		width: auto;
		height: 300px;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
	}
	.section.section_calculator {
		 text-align: left;
		padding-bottom: 77px;
		.title {
			text-align: left !important;
			max-width: 80%;
			margin-bottom: 24px;
			font-size: 35px;
			line-height: 35px;
		}
		.form_control {
			width: 100%;
			margin-bottom: 0;
		}

		.col--left {
			padding: 0 15px;
		}
		.form__error {
			top: 0;
		}
	}
	.calculator .btn {
		margin-top: 44px;
	}

	.footer {
		padding-top: 50px;

		.network {
			width: 100%;
			justify-content: flex-start;
			padding-bottom: 25px;
			border-bottom: 1px solid rgba(255,255,255,0.75);
			margin-bottom: 20px;

			.network_item {
				width: 20px;
				height: 20px;
				margin-right: 28px;
			}
		}

		.menu_partner {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.footer_link {
				img {
					width: 100px;
				}
			}
			.footer_partner:last-of-type {
				.footer_link  {
					img {
						width: 85px;
					}
				}
			}


		}

		.row {
			margin: 0;
		}
	}
	.footer_logo {
		width: 170px;
		margin-bottom: 16px;
	}
	.up_btn {
		width: 54px;
		height: 54px;
		right: 25px;
	}
	.footer_mobile {
		flex-basis: 100%;
		width: 100%;
	}

	#accordion {
		min-width: 100%;
		max-width: initial;
		width: 100%;
		margin-bottom: 8px;

		.accordion_header {
			background: transparent;
			color: #fff;
			font-size: 20px;
			line-height: 42px;
			display: flex;
			justify-content: space-between;
			font-family: helveticaBold, sans-serif;
			align-items: center;
			padding: 0;
			margin: 0;
			border: none;

			&.ui-accordion-header-active {
				margin-bottom: 10px;
			}

			&.ui-state-hover,
			&.ui-accordion-header-active,
			&:active,
			&:focus,
			&:hover {
				background: transparent;
				border: none;
				outline: none;
			}
		}

		.ui-accordion-header-icon.ui-icon.ui-icon-triangle-1-s,
		.ui-icon-triangle-1-e {
			display: none;
		}

		.accordion_body {
			background: transparent;
			color: #fff;
			border: none;
			padding: 0 0 10px;

			address,
			a,
			.footer_link {
				line-height: 34px;
				font-size: 18px;
				color: #fff;
				font-family: helveticaRegular, sans-serif;
			}
		}
	}

	.copyright {
		.row {
			margin: 0;
		}
		.col {
			padding: 20px 0;
		}

		.ursa_logo {
			display: block;

			img {
				width: 150px;
			}
		}
		font-size: 12px;
		line-height: 150%;
	}
}

@media (max-width: 450px) {
	.section_reviews .col--right img {
		width: 185%;
		height: auto;
	}
}
